<template>
  <div>
    <fTitle>
      <template #title>Skeleton 骨架屏</template>
      <template #introduce>在需要等待加载内容的位置设置一个骨架屏，某些场景下比 Loading 的视觉效果更好。</template>
    </fTitle>
    <sTitle>
      <template #title>动画效果</template>
      <template #introduce>绑定表格显示动画加载效果。</template>
    </sTitle>
    <div class="outbox">
    <div class="inbox">
      <el-row type="flex" justify="space-around">
    <el-col :span="20">
<div class="grid-content">
  <el-skeleton style="width:100%;height: 400PX;" :rows="10" :loading="load" animated>
  <el-table style="height: 400px;overflow: scroll;" :data="goodList">
    <el-table-column property="user.user_id" label="用户id" width="150"></el-table-column>
    <el-table-column property="create_time" label="评论时间" width="250"></el-table-column>
    <el-table-column property="content" label="评论" width="250"></el-table-column>
    <el-table-column  label="用户昵称">
      <template slot-scope="scope">
         <el-tag type="success">{{ scope.row.user.nick_name }}</el-tag>
      </template>
    </el-table-column>
  </el-table>
</el-skeleton>
</div>
    </el-col></el-row>
    </div></div>
    <sTitle>
      <template #title>Loading 状态</template>
      <template #introduce>当 Loading 结束之后，我们往往需要显示真实的 UI，可以通过 loading 的值来控制是否显示真实的 DOM。然后通过 具名 Slot 来设置当 loading 结束之后需要展示的 UI。</template>
    </sTitle>
    <div class="outbox">
    <div class="inbox">
      <el-row type="flex" justify="space-around">
    <el-col :span="20">
      <p>
      <label style="margin-right: 16px;">切换 Loading</label>
      <el-switch v-model="loading" />
    </p><div class="grid-content" style="height: 380px;">
      <el-skeleton style="width: 240px" :loading="loading" animated :throttle="500">
      <template slot="template">
        <el-skeleton-item
          variant="image"
          style="width: 240px; height: 240px;"
        />
        <div style="padding: 14px;">
          <el-skeleton-item variant="h3" style="width: 50%;" />
          <div
            style="display: flex; align-items: center; justify-items: space-between; margin-top: 16px; height: 16px;"
          >
            <el-skeleton-item variant="text" style="margin-right: 16px;" />
            <el-skeleton-item variant="text" style="width: 30%;" />
          </div>
        </div>
      </template>
      <template>
        <el-card>
          <img
            src="https://shadow.elemecdn.com/app/element/hamburger.9cf7b091-55e9-11e9-a976-7f4d0b07eef6.png"
            class="image"
          />
          <div style="padding: 14px;">
            <span>好吃的汉堡</span>
            <div class="bottom card-header">
              <span class="time">{{ currentDate }}</span>
              <el-button type="text" class="button">操作按钮</el-button>
            </div>
          </div>
        </el-card>
      </template>
    </el-skeleton></div>
    </el-col></el-row>
    </div></div>
  </div>

</template>

<script>
import request from '@/utils/request'
export default {
  name: 'ColorIndex',
  data () {
    return {
      load: true,
      goodList: [],
      loading: true,
      currentDate: '2021-06-01'
    }
  },
  created () {
    setTimeout(async () => {
      const goodsList = await request.get('/comment/list',
        {
          params: {
            scoreType: -1,
            goodsId: 10038,
            page: 1
          }
        })
      this.goodList = goodsList.data.list.data
      this.load = false
      console.log(goodsList.data.list.data)
    }, 2000)
  }

}
</script>

<style lang="less" scoped>
@import '../../styles/row.less';
.outbox{
    width: 100%;
    height: auto;
    background-color: #ffffff;
    display: flex;
    align-items: center;
    flex-direction:row;
    justify-content: center;
    .inbox{
      width: 85%;
      height: auto;
      background-color: #ffffff;
      border: 1px solid darkgray;
      .grid-content{
        height: auto;
        background: white;
        margin-bottom: 20px;
        text-align: start;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;}
    }}
</style>
